*{
  margin: 0;
  padding: 0;
}
.container{
  background: hsl(0, 0%, 0%);
  height: 100vh;
  overflow: hidden;
  display: flex;

}
img{
  width: 30%;
}
h1{
  font-size: 40px;
  font-weight: 600;
  color: #445a6f;
  margin: 40px 0;
}
button{
  width: 150px;
  height: 40px;
  border-radius: 5px;
  background-color: #445a6f;
  
  color: white;
  font-size: 20px;
  margin: 30px 0;
}
.wrapper{
  background: #fff;
  width: 600px;
  height: 400px;
  margin: auto;
  display: flex;
  align-items: center;
  flex-direction: column;

}